import styled from 'styled-components';

export const StyledMain = styled.main`
  padding: 16px;
  width: 100%;
  height: 100%;
  color: var(--color-white);

  position: absolute;
  top: 0;
  left: 0;

  h2 {
    margin: 40px 0 0 0px;
  }
  h2::first-letter {
    color: var(--color-pink);
  }

  p {
    line-height: 20px;
    font-size: var(--font-size-12);
  }

  .education p,
  .certificate p {
    margin: 0;
  }

  footer {
    height: 32px;
    line-height: 32px;
    font-size: var(--font-size-10);

    position: absolute;
    right: 16px;
    bottom: 0;
  }
`;

export const StyledSectionDescription = styled.section`
  margin: 0;
  padding: 8px 0;
  width: 100%;

  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 8px;

  font-size: var(--font-size-12);
  a:link, a:focus, a:visited {
    position: relative;
  }
  a:hover {
    text-decoration: underline;
  }
  a.anchor_github {
    display: inline-flex;
    align-items: center;
    grid-gap: 4px;
    gap: 4px;
  }
  
  p {
    margin: 12px 0;
    flex: 1;
  }

  img {
    width: 40%;
    height: 50%;
  }
  ul {
    font-size: var(--font-size-14);

    flex: 1;
    display: inline-flex;
    flex-direction: column;
    grid-gap: 4px;
    
    li::before {
      content: '-';
      display: inline-block;
      margin-right: 8px;
    }
  }
`;

export const StyledAboutSection = styled.section`
  p {
    margin: 4px 0;
    line-height: 20px;
    font-size: var(--font-size-12);
  }

  p:not(.greet) {
    text-indent: 4px;
  }
`;
export const StyledIntroduceSection = styled.section`
  font-size: var(--font-size-14);

  p:not(.greet) {
    text-indent: 4px;
  }
`;
export const StyledHashtagSection = styled.section`
  margin: 24px 0 0 0;
  width: 100%;

  h3 {
    font-size: var(--font-size-14);
    
  }
  ul {
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;  
    justify-content: center;
  }
  li {
    line-height: 1.3rem;
  }
`;

export const StyledHashTag = styled.span`
  display: inline-block;
  font-size: var(--font-size-12);
`;
